import { ReverseMethod, QueueBase } from './Shared';
export class RevQueue extends QueueBase {
    enqueue(item) {
        this.items
            .setDirection(0, RevQueue.reverseMethod)
            .push(item);
        return this;
    }
    dequeue() {
        if (this.isEmpty()) {
            throw new Error('Invalid Operation: Can not dequeue from an empty queue.');
        }
        return this.items
            .setDirection(1, RevQueue.reverseMethod)
            .pop();
    }
}
RevQueue.reverseMethod = ReverseMethod.Native;
